<script>
import gsap from "gsap";

export default {
  name: "TopLine",
  data() {
    return {
      nav_show: false,
    };
  },
  props: {
    isMobile: Boolean,
  },
  watch: {
    nav_show(val) {
      const body = document.querySelector("body");
      const scrollbarwidth = this.getScrollbarWidth();
      if (val) {
        body.style.cssText = `overflow: hidden; margin-right: ${scrollbarwidth}px; background: #fafafa`;
      } else {
        body.style.removeProperty("overflow");
        body.style.removeProperty("margin-right");
        body.style.removeProperty("background");
      }
    },
  },
  methods: {
    getScrollbarWidth() {
      // Creating invisible container
      const outer = document.createElement("div");
      outer.style.visibility = "hidden";
      outer.style.overflow = "scroll"; // forcing scrollbar to appear
      outer.style.msOverflowStyle = "scrollbar"; // needed for WinJS apps
      document.body.appendChild(outer);

      // Creating inner element and placing it in the container
      const inner = document.createElement("div");
      outer.appendChild(inner);

      // Calculating difference between container's full width and the child width
      const scrollbarWidth = outer.offsetWidth - inner.offsetWidth;

      // Removing temporary elements from the DOM
      outer.parentNode.removeChild(outer);

      return scrollbarWidth;
    },
    navChecker(e) {
      this.nav_show = !this.nav_show;
      const element = e.target.hasAttribute("data-custom")
        ? document.querySelector(`#${e.target.dataset.custom}`)
        : document.querySelector(`#${e.target.dataset.link}`);
      const offset = element.offsetTop;
      if (e.target.hasAttribute("data-custom")) {
        setTimeout(function() {
          gsap.to(window, {
            duration: 0,
            scrollTo: offset - e.target.dataset.offset,
            ease: "power1",
          });
        }, 200);
      } else {
        setTimeout(function() {
          gsap.to(window, {
            duration: 0,
            scrollTo: `#${e.target.dataset.link}`,
            ease: "power1",
          });
        }, 200);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.topline-wrapper {
  font-family: "Comfortaa", sans-serif;
  color: #2b2d42;
  font-weight: 600;
  font-size: calc(2vw + 10px);

  $transition--easing: cubic-bezier(0.77, 0, 0.175, 1);
  $transition--length: 0.8;
  $menu--items--count: 5;

  .menu-items {
    z-index: 101;
    position: fixed;
    width: 85vw;
    height: var(--viewHeight);
    top: 0;
    left: 15vw;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    pointer-events: none;

    .item {
      margin-bottom: 30px;
      transition: opacity 0.2s ease, transform 0.3s ease;
      opacity: 0;
      transform: translateX(-15%);
      width: fit-content;
      cursor: pointer;

      &:after {
        content: "";
        position: absolute;
        top: 105%;
        left: 0;
        height: 2px;
        width: 0;
        background: #4c5177;
        transition: width 0.2s ease-in-out;
      }

      &:hover {
        &:after {
          width: 100%;
        }
      }
    }
  }
  .topline {
    padding: 5px 20px;
    width: 100%;
    background: rgba(255, 255, 255, 0.692);
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.055);

    &:before,
    &:after {
      content: "";
      position: fixed;
      width: 100vw;
      height: 100vh;
      background: rgba(#eaeaea, 0.4);
      z-index: 100;
      top: 0;
      left: 0;
      transition: transform $transition--easing $transition--length + s;
      transform: translateX(0%) translateY(-100%);
    }

    &:after {
      background: rgba(#ffffff, 1);
      transition-delay: 0s;
    }
    &:before {
      transition-delay: 0.1s;
    }

    &.nav_active {
      + .menu-items {
        pointer-events: all;
        .item {
          pointer-events: all;
          opacity: 1;
          transform: translateX(0%);
          transition: opacity 0.3s ease, transform 0.3s ease, color 0.3s ease;
          @for $i from 0 through $menu--items--count {
            &:nth-child(#{$i}) {
              transition-delay: $transition--length * $i / 8 + 0.5 + s;
            }
          }
        }
      }

      &:before,
      &:after {
        transform: translateX(0%) translateY(0%);
      }

      &:after {
        transition-delay: 0.1s;
      }
      &:before {
        transition-delay: 0s;
      }
      .container #nav_trigger {
        .menu-line {
          background: rgb(87, 87, 87);
          &.line-left {
            transform: translateX(1px) rotate(45deg);
          }
          &.line {
            transform: rotate(-45deg);
          }
          &.line-right {
            transform: translateX(-2px) rotate(45deg);
          }
        }
      }
    }
    .container {
      width: 100%;
      display: grid;
      grid-template-columns: 35% 15%;
      column-gap: 50%;
